:root {
  --primary: #00909e;
  --secondary: #27496d;
  --third: #142850;
  --fourth: #dae1e7;
  --primary-light: #00adbd;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Ubuntu, "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--secondary);
  color: #fff;
}

a {
  text-decoration: none;
  color: #fff;
}

ul {
  list-style-type: none;
}

form {
  width: 100%;
  margin-top: 20px;
}


textarea {
  width: 100%;
  padding: 10px;
  height: 70px;
  font-size: 19px;
  border-radius: 5px;
  resize: none;
  outline: none;
}

button {
  width: 100%;
  height: 35px;
  background: var(--primary);
  color: #fff;
  font-size: 15px;
  font-weight: 800;
  border: none;
  outline: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.2s;
}

button:hover {
  background: var(--primary-light);
}

i {
  cursor: pointer;
}

h4 {
  font-weight: 800;
  margin-bottom: 3px;
}

img {
  height: 50px;
  border-radius: 50%;
}

/* Utilities */
.container {
  max-width: 700px;
  margin: auto;
  padding: 20px;
}

.modal-container {
   background-color: rgba(255, 255, 255, 0.3);
   position: fixed;
   top: 0;
   left: 0;
   right: 0;
   bottom: 0;
}